<template>
  <v-container>
    <v-row>
      <v-col>
        <v-card>
          <v-row justify="center" align="center" class="py-5">
            <v-col class="mx-auto" cols="10">
              <v-skeleton-loader
                v-if="
                  $store.state.loading.user.profile ||
                    $store.state.loading.user.user
                "
                type="card-avatar, article, card-actions"
              >
              </v-skeleton-loader>
              <v-row
                v-else
                class="justify-center jsutify-sm-start align-center"
              >
                <v-col cols="12" md="6" class="text-center">
                  <!-- <v-avatar rounded class="mx-auto"> -->
                  <v-img
                    v-if="user"
                    :src="
                      user.avatar
                        ? '/uploads/users/' + user.avatar + '?' + Date.now()
                        : '/potrait.jpg'
                    "
                    aspect-ratio="1"
                  >
                    <template v-slot:placeholder>
                      <v-row
                        class="fill-height ma-0"
                        align="center"
                        justify="center"
                      >
                        <v-progress-circular
                          indeterminate
                          color="primary"
                        ></v-progress-circular>
                      </v-row>
                    </template>

                    <div
                      v-if="user && $auth.user._id === user._id"
                      style="position:absolute; top:10px; right:10px; cursor:pointer; z-index:10000"
                    >
                      <crud-data-table
                        :default-item="user"
                        :button="{
                          color: 'primary',
                          text: ' ',
                          icon: 'mdi-camera',
                          type: 'icon',
                          iconSize: '50px',
                          iconColor: 'primary'
                        }"
                        status="edit"
                        :add-edit-dialog="{
                          editHeading: $t(
                            'addEditDialog.headings.edit.profile.image'
                          )
                        }"
                        :edit-data="{
                          url: '/api/users/:param',
                          param: '_id',
                          formData: true
                        }"
                        @after-edit="editUser"
                      >
                        <template v-slot:crud-form="{ item: user }">
                          <edit-profile-picture-form :user="user" />
                        </template>
                      </crud-data-table>
                    </div>
                  </v-img>
                  <!-- </v-avatar> -->
                </v-col>
                <v-col cols="12" md="6">
                  <div class="d-flex my-2 align-center">
                    <v-icon class="mr-3">mdi-account</v-icon>
                    <h3 v-if="user && user.name">{{ user.name }}</h3>
                  </div>

                  <div class="d-flex my-2 align-center">
                    <v-icon class="mr-3">mdi-email</v-icon>
                    <p v-if="user && user.email">{{ user.email }}</p>
                  </div>

                  <div class="d-flex my-2 align-center">
                    <v-icon class="mr-3">mdi-newspaper</v-icon>
                    <p v-if="user && user.role">{{ user.role.title }}</p>
                  </div>

                  <div class="d-flex my-2 align-center">
                    <v-icon class="mr-3">mdi-account</v-icon>
                    <p v-if="user && user.gender">{{ user.gender }}</p>
                  </div>

                  <div class="d-flex my-2 align-center">
                    <v-icon class="mr-3">mdi-comment</v-icon>
                    <p v-if="user && user.bio">{{ user.bio }}</p>
                  </div>
                </v-col>
              </v-row>
            </v-col>
            <v-col cols="10" class="mx-auto mt-8">
              <div class="d-flex flex-wrap justify-space-around align-center">
                <div v-if="user && $auth.user._id === user._id">
                  <crud-data-table
                    :default-item="user"
                    :button="{
                      text: 'Edit Profile',
                      icon: 'mdi-pencil'
                    }"
                    status="edit"
                    :add-edit-dialog="{
                      editHeading: $t(
                        'addEditDialog.headings.edit.profile.profile'
                      )
                    }"
                    :edit-data="{
                      url: '/api/users/:param',
                      param: '_id'
                    }"
                    @after-edit="editUser"
                  >
                    <template v-slot:crud-form="{ item }">
                      <edit-profile-form :user="item" />
                    </template>
                  </crud-data-table>
                </div>
                <div v-if="user && $auth.user.role.title === 'admin'">
                  <crud-data-table
                    :default-item="user"
                    :button="{
                      color: 'secondary',
                      text: ' Change Role',
                      icon: 'mdi-account'
                    }"
                    status="edit"
                    :add-edit-dialog="{
                      editHeading: $t(
                        'addEditDialog.headings.edit.profile.role'
                      )
                    }"
                    :edit-data="{
                      url: '/api/users/:param',
                      param: '_id'
                    }"
                    @after-edit="editUser"
                  >
                    <template v-slot:crud-form="{ item }">
                      <edit-role-form :user="item" />
                    </template>
                  </crud-data-table>
                </div>
                <div v-if="user && $auth.user._id === user._id">
                  <crud-data-table
                    :default-item="user"
                    :button="{
                      color: 'error',
                      text: 'Edit Credentials',
                      icon: 'mdi-lock'
                    }"
                    status="edit"
                    :add-edit-dialog="{
                      editHeading: $t(
                        'addEditDialog.headings.edit.profile.credentials'
                      )
                    }"
                    :edit-data="{
                      url: '/api/users/:param',
                      param: '_id'
                    }"
                    @after-edit="editUser"
                  >
                    <template v-slot:crud-form="{ item }">
                      <edit-credentials-form :user="item" />
                    </template>
                  </crud-data-table>
                </div>
              </div>
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import crud from '@/mixins/crud'
export default {
  middleware: ['pagePermission'],
  components: {
    CrudDataTable: () => import('@/components/crud/CrudDataTable'),
    EditProfileForm: () => import('@/components/forms/profile/EditProfileForm'),
    EditRoleForm: () => import('@/components/forms/profile/EditRoleForm'),
    EditProfilePictureForm: () =>
      import('@/components/forms/profile/EditProfilePictureForm'),
    EditCredentialsForm: () =>
      import('@/components/forms/profile/EditCredentialsForm')
  },
  mixins: [crud],
  data() {
    return {}
  },
  computed: {
    ...mapState({
      user: (state) => state.user.user
    })
  },
  watch: {
    $route() {
      if (this.$auth.user._id === this.$route.params.id) {
        this.$store.commit('user/SET_USER', this.$auth.user)
      } else {
        this.setUser({
          auth: this.$auth,
          axios: this.$axios,
          userId: this.$route.params.id
        })
      }
    }
  },
  mounted() {
    if (this.$auth.user._id === this.$route.params.id) {
      this.$store.commit('user/SET_USER', this.$auth.user)
    } else {
      this.setUser({
        auth: this.$auth,
        axios: this.$axios,
        userId: this.$route.params.id
      })
    }
  },
  methods: {
    ...mapActions('user', ['updateUser', 'setUser']),
    editUser(response) {
      this.updateUser({
        user: response.data.user,
        auth: this.$auth
      })
    }
  }
}
</script>

<style lang="scss" scoped>
p {
  margin: 0;
  padding: 0;
}
</style>
